$accent: #000;

html,
body {
  height: 100%;
}

#root {
  display: flex;
  height: 100%;
  flex-direction: column;
  border: 10px solid gray;
}

footer {
  display: flex;
  align-items: center;
  flex: 0 0 auto;
  padding: 10px 20px;
  border-top: 1px solid rgba($accent, 0.3);
  margin-top: auto;
  color: $accent;
  text-align: center;
}

// footer-logo
.footer-logo {
  flex: 0 1 auto;
  &__img {
    width: 80px;
  }
}

img {
  max-width: 100%;
}

button:focus {
  outline: none !important;
}

nav a.active {
  background-color: rgba($accent, 0.3) !important;
}

.copyright {
  font-weight: bold;
}
// footer bottom BEGIN

// LOGO begin
.logo {
  display: flex;
  align-items: center;
  color: rgba($accent, 0.7);
  text-decoration: none;
  transition: all 0.3s ease-in-out;
  &:hover {
    color: rgba($accent, 1);
    .logo__img {
      opacity: 1;
    }
  }
  &__img {
    width: 100px;
    margin-right: 10px;
    opacity: 0.8;
    transition: opacity 0.3s ease-in-out;
  }
  &__text {
    display: inline-block;
    vertical-align: top;
  }
  &__title {
    font-weight: 800;
    font-size: 25px;
    line-height: 1;
    display: block;
  }
}

.nav {
  display: inline-flex;
}

.copyright {
  margin: auto;
}

// Styles for tic-toc
.square {
  font: bold 24px/54px 'Jura', sans-serif;
  width: 100px;
  height: 100px;
  background: #fff;
  border: none;
  border-radius: 10px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  &--winner {
    background: #e0e0e0;
    svg * {
      stroke: #ed4b82;
    }
  }
}

.square:hover,
.square:focus {
  background: #e0e0e0;
  outline: none;
}

.board {
  width: auto;
  height: auto;
  display: grid;
  grid-template-columns: auto auto auto;
  gap: 6px;
  border-radius: 10px;
  transition: all 0.6s ease-in-out;
  &--disabled {
    animation: hide 0.4s 3s ease-in-out forwards;
  }
}

@keyframes hide {
  100% {
    opacity: 0;
  }
}
.board--disabled button.square {
  pointer-events: none !important;
  &:hover {
    background: none !important;
  }
}

.game {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  height: calc(100vh - 140px);
  flex-direction: row;
  &--board {
    position: relative;
    margin-top: 60px;
  }
  &--title {
    position: absolute;
    left: 0;
    right: 0;
    top: -120px;
  }
}

.play-again {
  position: absolute !important;
  left: 50%;
  transform: translateX(-50%);
  bottom: -55px;
}

.game-info {
  margin-left: 20px;
}

.status {
  position: absolute;
  left: 0;
  right: 0;
  top: -40px;
  font-size: 20px;
  font-weight: bold;
  color: #009688;
  margin-bottom: 10px;

  &--nextX {
    color: #606060;
  }
  &--win {
    animation: win 0.4s 3s ease-in-out forwards;
    color: #ed4b82;
  }
}

@keyframes win {
  100% {
    top: 30%;
    font-size: 50px;
    z-index: 1;
  }
}

.circle,
.first,
.second {
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

.circle {
  display: block;
  width: 50px;
  height: 50px;
  stroke-dasharray: 283;
  stroke-dashoffset: 283;
  animation: draw 1s ease-in-out forwards;
  margin: auto;
}

/* ------------------------------ X ANIMATION ----------------------------- */
.cross {
  display: block;
  width: 45px;
  height: 45px;
  margin: auto;
}

.first {
  stroke-dasharray: 125;
  stroke-dashoffset: 125;
  -webkit-animation: draw 0.6s forwards;
  animation: draw 0.6s forwards;
}

.second {
  stroke-dasharray: 125;
  stroke-dashoffset: 125;
  -webkit-animation: draw 0.6s 0.6s forwards;
  animation: draw 0.6s 0.6s forwards;
}

/* ------------------------------ X ANIMATION ----------------------------- */
@keyframes draw {
  100% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
}

/* ------------------------------ FULLSCREEN ----------------------------- */
.game:fullscreen {
  background: #eeeeee;
}

.game:-ms-fullscreen,
.game:-moz-full-screen,
.game:-webkit-full-screen {
  background: #eeeeee;
}
/* ------------------------------ FULLSCREEN ----------------------------- */

@import './media';
